/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
nav {
  color: #f9c411;
  background-color: #0794d1;
  padding: 1rem 0;
  }
  
  nav h2 {
    display: inline;
    margin: 0 2vw;
    font-size: 1.25rem;
    color: white;
  }
  
  nav a {
    margin: 0 2vw;
    font-size: 1rem;
    color: white;
    text-decoration: none;
  }
  nav a:hover {
    height: 100%;
    font-weight: bold;
  }
  nav a:visited {
    color: #f9c411;
  }

  header h1 {
    font-size: 6vw;
    text-align: center;
    color: white;
    padding: 5% 0 3%;
    text-shadow: 4px 4px 5px black;
  }

  h3 {
    font-size: 1.5rem;
  }
  
  #searchContainer, #resultsContainer {
    color: white;
    background-color: rgb(156, 124, 124);
    padding: 2vw;
    margin: 2vw;
    font-size: 3vw;
  }
  
  label {
    font-size: 1.25rem;
    margin: 2vw;
  }
  input {
    margin: 1vw 0;
    width: 96%;
    padding: 1.75vw;
    font-size: 1.25vw;
  }
  button {
    border-radius: 0;
    border-width: .25vw;
    border-style: double;
    background-color: #0794d1;
    color: white;
    padding: .75vw 4vw;
    font-size: 1.5vw;
    font-weight: bold;
  }
  button[disabled] {
    background-color: #f9e39f;
    color: gray;
    text-decoration: line-through;
  }
  form {
    padding: 1rem 0 1.5rem;
    position: relative;
  }
  form button {
    position: absolute;
    right: 0;
  }
